<template>
  <div class="home">
    <section class="search-box">
      <div class="keyword">
        <el-autocomplete
          v-model="keyword"
          :fetch-suggestions="querySearchAsync"
          placeholder="请输入内容"
          popper-class="keyword-autocomplete"
          @select="handleSelect">
          <template slot-scope="{ item }">
            <div class="item-line">
              <div class="title">{{ item.value }}</div>
              <span
                class="type"
                :class="{ port: item.type === '港口', city: item.type === '城市', county: item.type === '国家' }"
              >{{ item.type }}</span>
            </div>
          </template>
        </el-autocomplete>
        <el-button class="search-btn" icon="el-icon-search" type="primary" @click="search"></el-button>
      </div>
      <el-card class="result" v-loading="isLoading">
        <div slot="header" class="result-header">
          <span>查询结果</span>
        </div>
        <div v-if="tableData.length" class="table-data">
          <el-table
            :data="tableData"
            border
            header-row-class-name="t-header"
            style="width: 100%">
            <el-table-column
              prop="index"
              label="序号"
              width="60">
            </el-table-column>
            <el-table-column
              prop="riskLevel"
              label="风险等级"
              width="80">
            </el-table-column>
              <!-- prop="keyword" -->
            <el-table-column
              label="被查询词">
              <template slot-scope="scope">
                <span v-html="scope.row.keyword"></span>
              </template>
            </el-table-column>
            <el-table-column
              prop="type"
              label="类型"
              width="100">
            </el-table-column>
            <el-table-column
              prop="alias"
              label="别名">
            </el-table-column>
              <!-- prop="entityName" -->
            <el-table-column
              label="实体名称 (港口，城市，国家)"
              width="260">
              <template slot-scope="scope">
                <span v-html="scope.row.entityName"></span>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-else class="no-data">
          <figure>
            <img src="../assets/images/no-data.png" alt="">
          </figure>
          <p>暂无数据</p>
        </div>
      </el-card>
    </section>
    <div class="home-bg"></div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      risk_keys: [],
      timeout: null,
      keyword: '', // 搜索词
      isLoading: false, // 数据是否加载中
      tableData: []
    };
  },
  methods: {
    loadAll() {
      return [
        {
          value: 'Alexandrovsk【Alexport】, Alexandrovsk , Russia',
          type: '港口'
        },
        {
          value: 'Alexandrovsk , Russia',
          type: '城市'
        },
        {
          value: 'Russia',
          type: '国家'
        }
      ];
    },
    querySearchAsync(queryString, cb) {
      this.getRiskKeywordsList(queryString).then(res => {
        cb(res.data.result);
      })
    },
    handleSelect(item) {
      this.$set(this.$data, 'keyword', item.value)
    },
    search() {
      this.getRiskList()
    },
    async getRiskKeywordsList() {
      return await this.$axios.get('/api/risk_keys')
    },
    getRiskList() {
      this.$set(this.$data, 'isLoading', true)
      this.$axios.get('/api/risks', {
        params: {
          keyword: this.keyword // mock数据中输入0时显示无数据
        }
      }).then(res => {
        if(res.data.code === 200) {
          this.$set(this.$data, 'tableData', res.data.result)
        }
        this.$set(this.$data, 'isLoading', false)
      })
    }
  },
  mounted() {
    this.risk_keys = this.loadAll();
  }
};
</script>

<style lang="less" scoped>
.home {
  .search-box {
    padding-top: 110px;
    .keyword {
      display: flex;
      justify-content: center;
      align-items: center;
      /deep/ .el-autocomplete {
        height: 50px;
        width: 660px;
        .el-input {
          height: 100%;
          .el-input__inner {
            height: 100%;
            border-radius: 4px 0 0 4px;
          }
        }
      }
      .search-btn {
        font-size: 24px;
        height: 50px;
        width: 100px;
        border-radius: 0 4px 4px 0;
      }
    }
    .result {
      width: 1000px;
      margin: 40px auto;
      /deep/ .el-card__header {
        height: 44px;
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #7C261B;
      }
      .no-data {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 100px 0;
        figure {
          width: 166px;
          height: 116px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        p {
          margin-top: 16px;
          color: #666666;
        }
      }
      .table-data {
        /deep/ .el-table {
          color: #333333;
          .t-header {
            th {
              background-color: #F7F8FA;
              font-weight: normal;
              &:not(:last-child) {
                border-right: 1px solid transparent;
              }
            }
          }
          thead {
            color: #191919;
          }
        }
        /deep/ .el-table--small {
          font-size: 14px;
        }
      }
    }
  }

  .home-bg {
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: -1;
    width: 1366px;
    height: 502px;
    background: url('../assets/images/home-bg.png') no-repeat;
    background-position: bottom right;
    background-size: 100% auto;
  }
}
.keyword-autocomplete {
  li {
    .item-line {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .title {
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .type {
        min-width: 40px;
        height: 20px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        color: #b4b4b4;
        border: 1px solid red;
        border-radius: 2px;
        background: #F5F9FF;
        margin-left: 10px;
        &.port {
          color: #2E79F9;
          border: 1px solid #2E79F9;
          background: #F5F9FF;
        }
        &.city {
          color: #16BE6B;
          border: 1px solid #16BE6B;
          background: #F5FBF8;
        }
        &.country {
          color: #FF9900;
          border: 1px solid #FF9900;
          background: #FFFAF3;
        }
      }
    }

    .highlighted .type {
      color: #ddd;
    }
  }
}
</style>
